<template>
  <article class="gotoTop" v-show="visible">
    <a href="#0" class="topBtn" @click="goTop"><span></span></a>
  </article>
</template>

<script>
export default {
  name: 'gotoTop',
  props: {
    visibleHeight: {
      // 捲動按鈕出現條件
      type: Number,
      default: 350
    },
    rate: {
      type: Number, // 滾動速率
      default: 12
    }
  },
  data() {
    return {
      scrollTop: '',
      visible: false, // Show or Hide
      interval: null // Clock
    }
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll)
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll)
    if (this.interval) {
      clearInterval(this.interval)
    }
  },
  methods: {
    handleScroll() {
      // 判斷 Show Hide
      this.visible = window.pageYOffset > this.visibleHeight
      this.scrollTop = window.pageYOffset
    },
    goTop(e) {
      this.interval = setInterval(() => {
        this.scrollTop = this.scrollTop + -this.scrollTop / this.rate
        window.scrollTo(0, this.scrollTop)
        if (this.scrollTop <= 0) {
          window.scrollTo(0, 0)
          clearInterval(this.interval)
        }
      }, 13)
    }
  }
}
</script>

<style lang="scss" scoped>
.gotoTop {
  > a.topBtn {opacity: 0;position:fixed;bottom:1.5rem;right:1rem;z-index:100;width:40px;height:40px;background-color:rgba(0,0,0,.5);border-radius:50%;
    animation: fadeInUp 1.6s 1 forwards;
    &:hover {background-color:rgb(0, 160, 150);}
    > span {
      position:absolute;width:14px;height:2px;top:12px;left:50%;margin-left:-7px;background-color:#fff;
      &:before,&:after{position:absolute;content:"";}
      &:before{width:0;height:0;top:-2px;left:50%;margin-left:-7px;border-width:7px;border-style:solid;border-color:transparent transparent #fff;}
      &:after{width:6px;height:8px;background-color:#fff;left:50%;top:10px;margin-left:-3px;}
    }
    @keyframes fadeInUp {to {transform:translateY(-1rem);opacity:.5;}}
  }
}
</style>


