<template>
  <div class="slickCarousel">
    <slick ref="slick" :options="slickOptions">
      <a href="#0" class="imgBG1 d-flex align-items-end">
        <div class="slickInfo">
          <article class="row justify-content-center align-items-center">
            <section class="col-md-4">
              <!-- <img class="img-fluid m-auto" src="http://www.healthott.com/ott/images/public/about_p_002.png"> -->
              <img class="img-fluid m-auto" src="http://3k0p7trq4uw3mnwij2j2a5s1.wpengine.netdna-cdn.com/health/wp-content/uploads/sites/4/2015/06/health_img_top.png">
            </section>
            <section class="col-md-5">
              <h1>願景及目標</h1>
              <hr>
              <h5>能够協助醫療健康養護企業機構(醫院，診所，養護中心)醫生及護理人員提供客戶(病人)高品質及極貼心且滿意的服務</h5>
              <h5>提供具有大數據加上人工智慧處理能力的醫護健康服務平台</h5>
            </section>
          </article>
        </div>
      </a>
      <a href="#0" class="imgBG2 d-flex align-items-center">
        <div class="slickInfo">
          <article class="row justify-content-center align-items-center">
            <section class="col-sm-4">
              <h1>平台開發技術</h1>
              <hr>
              <h4>智慧雲數據搬遷服务平台</h4>
              <h4>醫康智能服務平台</h4>
            </section>
            <section class="col-sm-3">
              <img class="img-fluid m-auto" src="https://www.mdmarketingdigital.com/en/img/what-does-a-digital-marketing-agency-do.png">
              <!-- <img class="img-fluid m-auto" src="http://www.healthott.com/ott/images/public/about_p_003.png"> -->
            </section>
          </article>
        </div>
      </a>
      <a href="#0" class="imgBG3 d-flex align-items-end">
        <div class="slickInfo">
          <h1 class="text-center">提供企業客戶在智能轉型，資訊雲化</h1>
          <article class="row justify-content-center align-items-center">
            <!-- <section class="col-sm-3">
              <img class="img-fluid m-auto" src="http://www.healthott.com/ott/images/public/about_p_004.png">
            </section> -->
            <section class="col-sm-4">
              <h3><i class="fas fa-braille"></i> 服務升級過程中</h3>
              <h3><i class="fas fa-medkit"></i> 可靠安全的執行工具。</h3>
              <h3><i class="fas fa-user-md"></i> 快速簡易的客戶操作介面。</h3>
              <h3><i class="fab fa-buromobelexperte"></i> 可及時驗証的使用模式</h3>
            </section>
            <section class="col-sm-3">
              <img style="transform: translateY(1rem);" class="img-fluid m-auto" src="http://demo.stylishthemes.co/medic-wp/wp-content/uploads/2014/09/lady-doc.png">
            </section>
          </article>
          <!-- <img class="img-fluid m-auto" src="https://png.pngtree.com/element_our/md/20180326/md_5ab8da3829db5.png"> -->
        </div>
      </a>
    </slick>
  </div>
</template>

<script>
import Slick from 'vue-slick'
import '../../../node_modules/slick-carousel/slick/slick.css'
import '../../../node_modules/slick-carousel/slick/slick-theme.css'
export default {
  name: 'slickCarousel',
  components: {
    Slick
  },
  data() {
    return {
      slickOptions: {
        slidesToShow: 1,
        dots: true,
        arrows: false,
        speed: 800,
        adaptiveHeight: true,
        autoplay: true,
        autoplaySpeed: 3000
        // Any other options that can be got from plugin documentation
      }
    }
  },
  // All slick methods can be used too, example here
  methods: {
    next() {
      this.$refs.slick.next()
    },
    prev() {
      this.$refs.slick.prev()
    },
    // reInit() {
    //   // Helpful if you have to deal with v-for to update dynamic lists
    //   this.$nextTick(() => {
    //     this.$refs.slick.reSlick()
    //   })
    // }
  }
}
</script>

<style lang="scss" scoped>
.slickCarousel {
  overflow: hidden;
  background-color:white;
  a {
    color:#333;
    outline: none;
    min-height:30rem;
    background: url('http://3k0p7trq4uw3mnwij2j2a5s1.wpengine.netdna-cdn.com/health/wp-content/uploads/sites/4/2015/06/health_bg-1.jpg') no-repeat center center #ddd;
    // background: url('http://childrens-clinic.axiomthemes.com/wp-content/uploads/2017/07/slider_1_bg.jpg') no-repeat center center #ddd;
    background-size: cover;
    &:hover {text-decoration: none;}
  }
  // a.imgBG2 {background-image: url('http://childrens-clinic.axiomthemes.com/wp-content/uploads/2017/07/slider_2_bg.jpg')}
  // a.imgBG3 {background-image: url('http://childrens-clinic.axiomthemes.com/wp-content/uploads/2017/07/slider_3_bg.jpg')}
  a.imgBG2 {background-image: url('../../assets/slide_bg.jpg')}
  a.imgBG3 {background-image: url('http://demo.stylishthemes.co/medic-wp/wp-content/uploads/2014/09/slider_blue_021.jpg')}
  // a.imgBG3 {background-image: url('https://themes.wplook.com/health-medical/wp-content/uploads/sites/18/2015/07/slide1.jpg')}
  .slickInfo {
    padding:2rem 0 0;
    width:100%;
    // max-height:30rem;
    // background-color: rgba(0,0,70,.25);
    // background-color:rgba(50, 180, 185, .08);
    text-align:left;
    color:white;
    h1 {text-shadow:0 1px 3px rgba(0,0,0,.12);}
  }
}
</style>



